@import url(https://fonts.googleapis.com/css2?family=Besley:ital,wght@0,400;0,500;1,400;1,500&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}a{text-decoration:none}textarea:focus,input:focus{outline:none}#root,body,html{height:100%;width:100%;font-family:"Besley",serif}.page{position:absolute;left:15px;right:15px}.page-enter{opacity:0;-webkit-transform:scale(1.1);transform:scale(1.1)}.page-enter-active{opacity:1;-webkit-transform:scale(1);transform:scale(1);transition:opacity 300ms, -webkit-transform 300ms;transition:opacity 300ms, transform 300ms;transition:opacity 300ms, transform 300ms, -webkit-transform 300ms}.page-exit{opacity:1;-webkit-transform:scale(1);transform:scale(1)}.page-exit-active{opacity:0;-webkit-transform:scale(0.9);transform:scale(0.9);transition:opacity 300ms, -webkit-transform 300ms;transition:opacity 300ms, transform 300ms;transition:opacity 300ms, transform 300ms, -webkit-transform 300ms}

header{width:100%;padding:1.5rem 0;background-color:#fff}header .header-container{width:75%;margin:auto;display:grid;grid-template-columns:1fr auto auto;align-items:center;grid-column-gap:2rem;-webkit-column-gap:2rem;column-gap:2rem}header .header-container div.logo img{width:200px}header .header-container nav a{border-bottom:3px solid transparent;padding-bottom:1rem;letter-spacing:0.04rem;margin-left:2rem;font-size:1rem;color:#102757;cursor:pointer;transition:all 0.5s;font-weight:600}header .header-container nav a svg{margin-right:0.5rem;display:inline-block;color:rgba(0,0,0,0.5)}header .header-container nav a:hover{border-color:#00bcd4}header .header-container nav a:hover svg{color:#00bcd4}header .header-container div.navigation a{transition:all 0.5s;display:block;background-color:#102757;width:2.4rem;height:2.4rem;border-radius:50%;padding:0.7rem;box-sizing:border-box;text-align:center}header .header-container div.navigation a svg{color:#ffffff}header .header-container div.navigation a:hover{background-color:#00bcd4}

div.nav-wrapper{display:none;position:fixed;left:0;height:100%;width:100%;top:0;background-color:#102757;z-index:100;align-items:center}div.nav-wrapper.open{display:grid !important}div.nav-wrapper nav a{font-size:2rem;display:block;text-align:center;letter-spacing:0.2rem;line-height:5rem;color:#ffffff;transition:all 0.3s ease-in-out}div.nav-wrapper nav a:hover,div.nav-wrapper nav a.active{background-color:#00bcd4}

.slide-container{position:relative}.slide-container .image-container-inner{display:grid;position:absolute;left:0;top:0;width:100%;height:100%;align-items:center}.slide-container .image-container-inner h1{display:block;text-align:center;font-style:italic;letter-spacing:0.1rem;font-size:3rem;margin:1rem 0 2rem 0;color:#fff;text-shadow:0 4px 4px #102757}.slide-container img{width:100%}

section.home_component{text-align:center;background-color:#f5f5f5;padding:5rem 0}section.home_component div.container{width:75%;margin:auto}section.home_component div.container h1{font-size:2rem;font-weight:bolder;letter-spacing:0.1rem;color:#102757}section.home_component div.container h2{font-style:italic;letter-spacing:0.1rem;font-size:1.5rem;margin:1rem 0 2rem 0;color:#00bcd4}section.home_component div.container p{font-size:1.1rem;letter-spacing:0.06rem;line-height:2rem}

section.service_component div.container div.item{display:grid;grid-template-columns:1fr 1fr}section.service_component div.container div.item:nth-child(odd) article{grid-column:1}section.service_component div.container div.item:nth-child(odd) div.background{grid-column:2;grid-row:1}section.service_component div.container div.item div.background{background-size:cover;background-position:center center}section.service_component div.container div.item article{padding:12%}section.service_component div.container div.item article h1{font-size:1.5rem;font-weight:bolder;letter-spacing:0.1rem;text-transform:uppercase;color:#102757;line-height:180%}section.service_component div.container div.item article div.separator{background-color:#00bcd4;display:inline-block;width:3rem;height:0.3rem;margin:2rem 0 3rem 0}section.service_component div.container div.item article p{font-size:1.1rem;letter-spacing:0.06rem;line-height:2rem}section.service_component div.container div.item article p div.member{display:grid;grid-template-columns:1fr 8rem;grid-column-gap:5rem;-webkit-column-gap:5rem;column-gap:5rem}section.service_component div.container div.item article p div.member img{width:100%}

section.contact_component{background-color:#102757;padding:5rem 0}section.contact_component div.container{width:75%;margin:auto}section.contact_component div.container div.content{text-align:center}section.contact_component div.container div.content h1{font-size:2rem;font-weight:bolder;letter-spacing:0.1rem;text-transform:uppercase;color:#102757;margin:1rem 0 2rem 0;color:#00bcd4}section.contact_component div.container div.content p{font-size:1.1rem;letter-spacing:0.06rem;line-height:2rem;color:#ffffff}section.contact_component div.container nav{text-align:center;margin:2rem 0}section.contact_component div.container nav a{border-bottom:3px solid transparent;padding-bottom:1rem;letter-spacing:0.04rem;margin-left:2rem;font-size:1rem;color:#ffffff;cursor:pointer;transition:all 0.5s;font-weight:600}section.contact_component div.container nav a svg{margin-right:0.5rem;display:inline-block;color:#00bcd4}section.contact_component div.container nav a:hover{border-color:#00bcd4}section.contact_component div.container nav a:hover svg{color:#00bcd4}section.contact_component div.container form{margin:5rem 0;width:100%;display:grid;grid-template-columns:1fr 1fr 1fr;grid-column-gap:1rem;-webkit-column-gap:1rem;column-gap:1rem;grid-row-gap:1rem;row-gap:1rem}section.contact_component div.container form fieldset:nth-child(4){grid-column-start:1;grid-column-end:4}section.contact_component div.container form fieldset:nth-child(5){grid-column-start:1;grid-column-end:3}section.contact_component div.container form input,section.contact_component div.container form textarea{font-family:"Besley",serif;width:100%;box-sizing:border-box;font-size:14px;letter-spacing:0.1rem;display:block;border:0;padding:15px 20px;background:rgba(0,0,0,0.1);border:1px solid #243b55;color:#fff;transition:all 1s ease}section.contact_component div.container form input ::-webkit-input-placeholder, section.contact_component div.container form textarea ::-webkit-input-placeholder{color:rgba(255,255,255,0.7);opacity:1}section.contact_component div.container form input :-ms-input-placeholder, section.contact_component div.container form textarea :-ms-input-placeholder{color:rgba(255,255,255,0.7);opacity:1}section.contact_component div.container form input ::placeholder,section.contact_component div.container form textarea ::placeholder{color:rgba(255,255,255,0.7);opacity:1}section.contact_component div.container form textarea{resize:none}section.contact_component div.container form button{width:100%;box-sizing:border-box;font-size:14px;letter-spacing:0.1rem;display:block;border:0;padding:15px 20px;background:rgba(0,0,0,0.1);border:1px solid #102757;color:#fff;transition:all 1s ease;background-color:#00bcd4;cursor:pointer}section.contact_component div.container form button:hover{border:1px solid #00bcd4;background-color:transparent}

